	import { people_infos,user_add_follow,get_tags,home_info,question_add } from '../../api/api.js'
	import uniPopup from "../../components/uni-popup/uni-popup.vue"
	export default {
		components: {
			uniPopup
		},
		data() {
			return {
				question: '',
				to_member_id: '',//达人id
				question_content: '',
				imgList: [],
				peopleDetail: {},
				checkedList: [],//已选中标签列表
				tagList: [],
				tags: '',
				moneyTab: 0,
				moneyNum: 5,
				totalMoeny: 0,
				moneyList: [
					{
						label: '5',
						value: 5
					},
					{
						label: '10',
						value: 10
					},
					{
						label: '20',
						value: 20
					},
					{
						label: '50',
						value: 50
					},
				],
				videoSrc:''
			}
		},
		onLoad(option) {
			let that = this
			// that.to_member_id = option.id
			that.to_member_id = 24
			uni.getStorage({
			    key: 'access_token',
			    success: function (res) {
			        that.access_token = res.data
			    }
			});
			uni.getStorage({
			    key: 'member_id',
			    success: function (res) {
			        that.member_id = res.data
					
			    }
			});
			console.log(that.access_token)
			console.log(that.member_id)
			that.getPeopleDetail()
			that.getMoney()
		},
		methods: {
			//获取达人详情
			getPeopleDetail(){
				let that = this
				people_infos({
					member_id: this.member_id,
					access_token: this.access_token,
					to_member_id: that.to_member_id
				}).then(res => {
					console.log(res)
					if(res.data.code == 1){
						that.peopleDetail = res.data.data
					}
				})
			},
			// 获取余额
			getMoney(){
				let that = this
				home_info({
					member_id: that.member_id,
					access_token: that.access_token
				}).then(res => {
					console.log(res)
					if(res.data.code == 1){
						that.totalMoeny = res.data.data.balance
					}
				})
			},
			//关注，取消关注达人
			focusPeople(type){
				let that = this
				user_add_follow({
					member_id: that.member_id,
					access_token: that.access_token,
					to_member_id: that.to_member_id
				}).then(res => {
					console.log(res)
					let msg
					if(res.data.code == 1){
						if(type == 1){
							 msg = "关注达人成功!"
						}else if(type == 2){
							msg = "取消成功!"
						}
						uni.showToast({
							title: msg,
							duration: 2000,
							icon: 'none'
						});
						that.getPeopleDetail()
					}else{
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon: 'none'
						});
					}
				})
			},
			// 选择金额
			selectMoney(index){
				this.moneyTab = index
				this.moneyNum = this.moneyList[index].value
			},
			// 填写金额
			inputMoney(){
				this.moneyTab = -1
			},
			// 打开弹出层
			openPopup(){
				console.log("ffffff")
				console.log(this.$refs.popup)
				this.$refs.popup.open()
				this.getTags()
			},
			// 关闭弹出层
			closePopup(){
				this.$refs.popup.close()
			},
			//获取标签列表
			getTags(){
				let that = this
				get_tags({
					member_id: this.member_id,
					access_token: this.access_token
				}).then(res => {
					console.log(res)
					if(res.data.code == 1){
						that.tagList = res.data.data.list
						console.log(that.tagList)
						// 把所有标签置为未选中
						that.tagList.forEach((item,index) =>{
							item.child.forEach((childItem,index1) =>{
								childItem.is_checked = 0
							})
						})
					}
				})
			},
			// 选中标签-
			checkTag(index,tagsIndex){
				let that = this
				that.tagList[index].child[tagsIndex].is_checked = 1
			},
			// 取消选中
			cancelTag(index,tagsIndex){
				let that = this
				that.tagList[index].child[tagsIndex].is_checked = 0
			},
			// 标签选择确定
			sure_tag(){
				let that = this
				that.checkedList = []
				that.tagList.forEach((item,index1) =>{
					item.child.forEach((items,index2) =>{
						if(items.is_checked == 1){
							that.tags += items.id + ","
							that.checkedList.push(items.tag_title)
						}
					})
				})
				//去除最后一个逗号
				that.tags = that.tags.substr(0, that.tags.length-1);
				that.$refs.popup.close()
			},
			// 重置标签
			reset_tag(){
				let that = this
				that.checkedList = []
				that.tagList.forEach((item,index1) =>{
					item.child.forEach((items,index2) =>{
						items.is_checked = 0
					})
				})
			},
			//点击上传图片
			uploadImg(){
			   let that = this	 
				uni.chooseImage({
					count: 3, //上传文件数量
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album','camera'], //从相册选择
					success:(res)=> {
					  console.log(res)
					  if (that.imgList.length != 0) {
						  that.imgList = that.imgList.concat(res.tempFilePaths)
					  } else {
						  that.imgList = res.tempFilePaths
					  } 
					  console.log(that.imgList)
					 //  let imgs = that.imgList.map((value,index)=>{
					 //  	return {	
					 //  		name:'image'+index,
					 //  		uri:value
					 //  	}
					 //  });
					 //  console.log(imgs)
					 //  uni.uploadFile({
					 //  	url: 'http://test.api.lanchonggk.com/industry/api/add_pics',
					 //  	fileType: 'image',
					 //  	files: imgs,
					 //  	filePath: res.tempFilePaths[0],
					 //  	name: 'pic[]',
					 //  	formData:{
					 //  		'post_id': 44,
					 //  		'type': '1'
					 //  	},
					 //  	 success: function(res) {
					 //  		 console.log(res)
						// },
						// })
					}
				});
			},
			//上传视频
			uploadVideo(){
				let that = this
				uni.chooseVideo({
					count: 1,
					sourceType: ['camera', 'album'],
					success: function (res) {
						console.log(res)
						that.videoSrc = res.tempFilePath;
					}
				})
			},
		// 发布
		submitBtn(){
			let that = this
			question_add({
				member_id: that.member_id,
				access_token: that.access_token,
				to_member_id: that.to_member_id,
				title: that.question,
				content: that.question_content,
				price: that.moneyNum,
				tag_id: that.tags
			}).then(res => {
				console.log(res)
				console.log(res.data.data)
				let post_id = res.data.data
				//如果上传了图片
				if(that.imgList.length > 0 ){
					let imgs = that.imgList.map((value,index)=>{
						return {	
							name:'image'+index,
							uri:value
						}
					});
					console.log(imgs)
					uni.uploadFile({
						url: 'http://test.api.lanchonggk.com/industry/api/add_pics',
						fileType: 'image',
						files: imgs,
						filePath: res.tempFilePaths[0],
						name: 'pic[]',
						formData:{
							'post_id': post_id,
							'type': '1'
						},
						 success: function(res) {
							 console.log(res)
							 if(res.statusCode == 200){
								 if(that.videoSrc){//如果有视频
									 // 视频接口
									 uni.uploadFile({
									 	url: 'http://test.api.lanchonggk.com/industry/api/add_video',
									 	filePath: that.videoSrc,
									 	fileType: 'video',
									 	name: 'video',
									 	formData: {
									 	  'post_id': post_id,
									 	  'type': '1'
									 	},
									 	success: (res) => {
									 	  console.log(res);
									 	  if(res.statusCode == 200){
									 		 uni.showToast({
									 			title: '问题发布成功!',
									 			duration: 2000,
									 			icon: 'none'
									 		 });
									 		 //跳转问题详情页
									 		 uni.navigateTo({
									 			url: '../questionDetail/questionDetail?id='+post_id,
									 		 })
									 	  }
									 	}
									 });
								 }else{
									 uni.showToast({
										title: '问题发布成功!',
										duration: 2000,
										icon: 'none'
									});
									 // 跳转问题详情页
									 uni.navigateTo({
									 	url: '../questionDetail/questionDetail?id=' + post_id,
									 })
								 }
								 
							 }
						},
						fail: function (err) {
							console.log(err)
						}
					})
				}
			})
		}	
	}
}
